
"use client";

import { Briefcase } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { useLanguage } from '@/context/language-context';

export default function WorkPage() {
  const { translations } = useLanguage();
  const workExperience = translations.work.experiences;

  return (
    <section id="work" className="py-16 sm:py-24 bg-secondary/50 h-full">
      <div className="container">
        <div className="flex items-center gap-4 mb-12">
            <Briefcase className="w-8 h-8 text-primary" />
            <h2 className="font-headline text-3xl sm:text-4xl font-bold">{translations.work.title}</h2>
        </div>
        <div className="space-y-8">
          {workExperience.map((exp) => (
            <Card key={exp.company}>
              <CardHeader>
                <div className="flex justify-between items-start">
                    <div>
                        <CardTitle className="font-headline text-xl">{exp.role}</CardTitle>
                        <p className="text-primary font-medium">{exp.company}</p>
                    </div>
                    <p className="text-sm text-muted-foreground">{exp.period}</p>
                </div>
              </CardHeader>
              <CardContent className="text-muted-foreground space-y-4">
                {exp.description.map((paragraph, index) => {
                  const dboardRegex = /(Project|Projeto): DBoard/g;
                  if (dboardRegex.test(paragraph)) {
                    const parts = paragraph.split(dboardRegex);
                    return (
                      <p key={index}>
                        {parts[0]}
                        {translations.work.project_prefix} <a href="https://apps.apple.com/br/app/dboard/id6747050797" target="_blank" rel="noopener noreferrer" className="text-primary hover:underline">DBoard</a>
                      </p>
                    );
                  }
                  return <p key={index}>{paragraph}</p>;
                })}
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}
